.team-counter-container {
  background: #404040;
  border-radius: 35px 35px 35px 35px;
  width: 150px;
  height: 50px;
  color: white;
  display: grid;
  grid-template-columns: 50px 100px;
  grid-template-rows: auto;
}

.team-counter-text {
  color: white;
  font-size: 25pt;
  font-weight: bold;
  justify-self: end;
  margin-right: 15px;
  align-self: center;
}

.team-counter-icon {
  max-height: 100%;
}